<template>
  <v-navigation-drawer v-model="navDrawer" @transitionend="afterTransition" temporary app>
    <v-list>
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title class="headline">{{ project.team_name }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <template v-for="member in project.members">
        <v-list-item v-if="member.verified" :key="member.email" two-line>
          <v-list-item-avatar>
            <v-avatar :color="member.color">
              <span class="white--text">{{ member.last_name.charAt(0) }}</span>
            </v-avatar>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title>{{ member.first_name }} {{ member.last_name }}</v-list-item-title>
            <v-list-item-subtitle>{{ member.position }}</v-list-item-subtitle>
          </v-list-item-content>

          <!-- <v-icon v-if="member.isUser" color="blue darken-4">mdi-star</v-icon> -->
        </v-list-item>
      </template>

      <v-list-item @click="inviteDialog = true">
        <v-list-item-icon>
          <v-icon>mdi-plus</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title> </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>

    <v-divider></v-divider>

    <v-list>
      <v-list-item @click="$router.push({ name: 'user' })">
        <v-list-item-icon>
          <v-icon>mdi-home</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title></v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-list-item>
        <v-list-item-icon>
          <v-icon>mdi-settings</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title></v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-list-item>
        <v-list-item-icon>
          <v-icon>mdi-help-circle</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title></v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-list-item>
        <v-list-item-icon>
          <v-icon>mdi-information</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title></v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>

    <v-footer
      class="mb-2 transparent body-2"
      fixed
    >&copy; {{ new Date().getFullYear() }} DoTS. All rights reserved.</v-footer>

    <v-dialog v-model="inviteDialog" persistent max-width="600">
      <v-card tile>
        <v-card-title class="headline"> </v-card-title>
        <v-card-text>
            .     .
          <br />     .
          <br />, DoTS     .
        </v-card-text>

        <v-text-field v-model="memberEmail" single-line solo-inverted class="mx-4" @keypress.enter="findUser"></v-text-field>

        <v-card-actions>
          <div class="flex-grow-1"></div>

          <v-btn color="green darken-1" text tile @click.stop="cancel"></v-btn>
          <v-btn color="blue darken-4" text tile @click.stop="findUser"> </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="isUserNotValid" persistent max-width="600">
      <v-card tile>
        <v-card-title class="headline"></v-card-title>
        <v-card-text>
            .        .
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>

          <v-btn color="blue darken-4" text tile @click.stop="isUserNotValid = !isUserNotValid"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="isEmailEmpty" persistent max-width="600">
      <v-card tile>
        <v-card-title class="headline"></v-card-title>
        <v-card-text>
           .
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>

          <v-btn color="blue darken-4" text tile @click.stop="isEmailEmpty = !isEmailEmpty"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="isSucceeded" persistent max-width="600">
      <v-card tile>
        <v-card-title class="headline"> </v-card-title>
        <v-card-text>
               .
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>

          <v-btn color="blue darken-4" text tile @click.stop="cancel"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-navigation-drawer>
</template>

<script>
import jwtDecode from 'jwt-decode';

export default {
  name: 'nav-drawer',
  props: {
    project: {
      type: Object
    },
    drawer: {
      type: Boolean
    }
  },
  data () {
    return {
      inviteDialog: false,
      memberEmail: '',
      isUserNotValid: false,
      isEmailEmpty: false,
      isSucceeded: false,
      navDrawer: this.drawer,
      isDrawerOpened: false
    }
  },
  watch: {
    drawer (val) {
      this.navDrawer = val
    }
  },
  methods: {
    cancel () {
      this.inviteDialog = false
      this.isSucceeded = false
      this.memberEmail = ''
    },
    findUser () {
      if (this.memberEmail === '') {
        this.isEmailEmpty = true
      } else {
        this.$http
          .get('/users', { params: { email: this.memberEmail } })
          .then(result => {
            if (!result.data) {
              this.isUserNotValid = true
              throw new Error('Invalid user email')
            } else {
              const decoded = jwtDecode(localStorage.getItem('userToken'))

              this.$http.post('/mail/invite', {
                email: this.memberEmail,
                sender: decoded,
                newMember: result.data,
                project: this.project
              })
            }
          })
          .then(result => {
            this.isSucceeded = true
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    afterTransition() {
      //          
      //       
      if (!this.isDrawerOpened) {
        this.isDrawerOpened = true
      } else {
        this.$emit('update')
        this.isDrawerOpened = false
      }
    }
  }
}
</script>
